@import '../../../utils/style/media-queries';

.star {
  width: 25px;
  height: 25px;
  margin: 2px;
  float: left;
  background-color: var(--dark-grey);
  -webkit-clip-path: polygon(
    50% 0%,
    63% 38%,
    100% 38%,
    69% 59%,
    82% 100%,
    50% 75%,
    18% 100%,
    31% 59%,
    0% 38%,
    37% 38%
  );
  @include mobile {
    width: 15px;
    height: 15px;
  }
  &_selected {
    background-color: var(--primary);
  }
}
