.card {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  position: relative;
  background-color: var(--primary);
  border-radius: var(--border-radius);
  &__link {
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    border-radius: var(--border-radius);
    overflow: hidden;
    transition: all ease-in-out 0.3s;
    cursor: pointer;
    z-index: 2;
    &::after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background: linear-gradient(
        180deg,
        rgba(255, 255, 255, 0) 0%,
        rgba(0, 0, 0, 0.5) 100%
      );
      border-radius: var(--border-radius);
    }
    &:hover {
      box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
      transition: all ease-in-out 0.3s;
      img {
        transform: scale(1.25);
        transition: all ease-in-out 1s;
      }
    }
    &__img {
      width: 100%;
      object-fit: cover;
      border-radius: var(--border-radius);
      transition: all ease-in-out 1s;
    }
    &__title {
      position: absolute;
      left: 0;
      bottom: 0;
      padding: 0 1.111rem 1.111rem 1.111rem;
      color: var(--secondary);
      font-size: 1rem;
      z-index: 3;
    }
  }
}
